<template>
  <div class="page page-virtual-account">
    <div class="titles">
      <div class="num">
        我的微信支付
        <span class="secondary"> （共{{ wechat.list.length }}张） </span>
      </div>
      <van-button icon="plus" icon-position="left" replace :to="`/add_payment/4${isMerchant ? '?m=1' : ''}`">
        添加微信
      </van-button>
    </div>
    <div class="collection">
      <van-swipe-cell v-for="(item, index) in wechat.list" :key="index">
        <div class="paymentCard">
          <div class="icon wechat"></div>
          <div class="info">
            <div>微信真实姓名：{{ item.wxRealname |  safeText }}</div>
            <div>姓名：{{ item.realName | safeRealName }}</div>
          </div>
          <van-image :src="$root.getImageUrl(item.receiptCode)" class="qr">
          </van-image>
        </div>
        <template #right>
          <van-button square text="解绑" type="danger" @click="unbind(item.id)"/>
        </template>
      </van-swipe-cell>
    </div>
  </div>
</template>


<script>
import {unBind} from "@/apis/user"
import {verifyGoogleAuth, verifySecuretedCode} from "@/vendor/globalVerify";
import { cryptoPaymentsMixins } from '@/vendor/mixins';

export default {
  mixins:[cryptoPaymentsMixins],
  computed: {
    wechat() {
      return this.payments.wechat;
    },
  },
  created() {
    this.$root.$emit("root:set-topbar", [
      "left-back",
      "notices",
      "customer-service",
    ]);
  },
  methods: {
    async unbind(id) {
      const isPassed = await verifyGoogleAuth(this)
      if(!isPassed) return
      const securePassword = await verifySecuretedCode(this)
      if (securePassword && securePassword.length === 6) {
        const {ok, msg} = await unBind({id, securePassword})
        if (ok) {
          this.$root.loadPayments()
        } else {
          this.$toast.fail(msg)
        }
      }
    }
  },
};
</script>


